<template>
  <el-form class="form" :inline="true" @submit.native.prevent="onSubmit" :model="form" label-width="100px">
    <el-breadcrumb separator="/" style="margin-bottom: 30px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/article/questionlist">问题列表</el-breadcrumb-item>
      <el-breadcrumb-item>详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="24">
      <el-col>
        <el-form-item label="问题标题">
          <el-input v-model="form.problem" placeholder="请输入商品标题"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="问题排序">
          <el-input type="number" v-model="form.sort" placeholder="请输入问题排序"></el-input>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="问题分类">
          <el-select v-model="form.pid" placeholder="请选择">
            <el-option v-for="item in tagList" :key="item.id" :label="item.title" :value="item.id"> </el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item label="答案" style="display: flex;">
          <el-input
            size="small"
            type="textarea"
            :rows="12"
            :cols="40"
            placeholder="请输入内容"
            v-model="form.content"
          />
        </el-form-item>
      </el-col>
      <el-col>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>
<script>
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  components: {},
  data() {
    return {
      config: [],
      file: [],
      form: {
        content: '',
        sort: '',
        pid: '',
        problem: '',
      },
      tagList: [],
    }
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  mounted() {
    const id = this.$router.history.current.params.id

    if (id > 0) {
      Fetch('article/problemanswer/view', { id }).then(r => {
        this.form = r.data.view
        this.config = r.data.config
      })
    }
    this.getTag()
  },
  methods: {
    getTag() {
      Fetch('/article/problem/search')
        .then(r => {
          this.tagList = r.data.list
          console.log(this.tagList)
        })
        .catch(res => {
          Message.error(res.msg)
        })
    },
    fileSuccess1(res, file) {
      this.file = [file]
      this.form.thumb = res.data.file
    },
    onDelct1(file, fileList) {
      this.file1 = fileList
      this.form.thumb = fileList.map(item => item.name)
    },
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      console.log(formData)
      formData.append('file', file)
      Fetch('/api/api/upload', formData).then(result => {
        let url = result.data.file
        Editor.insertEmbed(cursorLocation, 'image', url)
        resetUploader()
      })
    },
    onSubmit() {
      const id = this.$router.history.current.params.id
      Fetch('/article/problemanswer/' + (id ? 'update' : 'create'), {
        id: this.$router.history.current.params.id,
        ...this.form,
      }).then(() => {
        Message.success('操作成功')
        this.$router.replace('/article/questionlist')
      })
    },
  },
}
</script>
<style lang="less" scoped>
.form {
  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
